@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1200px;
  }
}
.page #video {
  background: #d9d9d9;
  padding-bottom: 80px;
}
.page#page-sobre-nos .container-text-image .compromisso {
  background-color: rgba(0, 0, 0, 0.22);
  border-radius: 5px;
  padding: 45px;
}
.page#page-sobre-nos .container-text-image .compromisso p {
  margin-bottom: 0;
}
.page#page-nossa-historia .container-text-image {
  position: relative;
  background: #006635;
  z-index: 0;
}
.page#page-nossa-historia .container-text-image::before {
  content: "";
  position: absolute;
  top: 2%;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../../assets/images/before_sobre_sec1.png") no-repeat left bottom;
  background-size: contain;
  opacity: 0.2;
  z-index: -1;
}
.page#page-nossa-historia .container-marcos {
  position: relative;
  background: #006635;
  padding: 60px 0 80px;
  z-index: 0;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 70%;
  height: 650px;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-snap-type: x mandatory;
  padding-left: 7px;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item .content-marco {
  position: relative;
  height: 321.5px;
  gap: 30px;
  cursor: pointer;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item .content-marco .dot-top {
  content: "";
  position: absolute;
  top: calc(20% - 15px);
  left: -7px;
  width: 15px;
  height: 15px;
  background-color: #006635;
  border: 2px solid #fff;
  border-radius: 50%;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item .content-marco .dot-bottom {
  content: "";
  position: absolute;
  bottom: -7.5px;
  left: -7px;
  width: 15px;
  height: 15px;
  background-color: #006635;
  border: 2px solid #fff;
  border-radius: 50%;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item .content-marco::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 1px;
  height: 80%;
  background-color: #cae098;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(odd) {
  align-items: flex-start;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(odd) .content-marco {
  padding: 0 60px 30px 60px;
  border-bottom: 1px solid #cae098;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(odd) .content-marco .texts {
  justify-content: center;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(even) {
  align-items: flex-end;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(even) .content-marco {
  padding: 30px 60px 0 60px;
  border-top: 1px solid #cae098;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(even) .content-marco .dot-top {
  top: -7.5px;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(even) .content-marco .dot-bottom {
  bottom: calc(20% - 15px);
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(even) .content-marco::before {
  top: 0;
  bottom: inherit;
}
.page#page-nossa-historia .container-marcos::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../../assets/images/before-section.png") no-repeat left bottom;
  background-size: cover;
  opacity: 0.03;
  filter: invert(1);
  z-index: -1;
}
.page#page-areas-de-atuacao #engenharia-civil {
  background-color: #006635;
  padding: 30px 0 80px;
}
.page#page-areas-de-atuacao #engenharia-civil h2 {
  font-size: 20px;
  line-height: 24px;
  color: #fff;
}
.page#page-areas-de-atuacao #engenharia-civil h2 span {
  color: #cae098;
}
.page#page-areas-de-atuacao #engenharia-civil .lista-engenharia-civil {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 15px 60px;
}
.page#page-areas-de-atuacao #engenharia-civil .lista-engenharia-civil .item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
}
.page#page-areas-de-atuacao #engenharia-civil .lista-engenharia-civil .item h3 {
  font-size: 16px;
  line-height: 20px;
  color: #fff;
  font-weight: lighter;
}
.page#page-nossos-servicos h3 {
  font-size: 16px;
  line-height: 20px;
}
.page#page-nossos-servicos a:hover {
  border-color: #cae098;
}
.page#page-nossos-servicos .show-after {
  padding-bottom: 80px;
}